@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);

body {
  height: 100%;
  margin: 0;
  background-color: #FFFFFF;
}
#profile__img {
    border-radius: 275px;
}

#slack {
    display: none;
}

#profile_section p {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    color: #101828;
    margin-top: 7px;
}

#web-share {
    position: relative;
    top: -200px;
    left: 325px;
}

#mobile-share {
    display: none;
}

#links_section a {
    text-decoration: none;
    background-color: #EAECF0;
    border: 1px solid #EAECF0;
    border-radius: 8px;
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    width: 700px;
    height: 70px;
}

.button {
    background-color: #EAECF0;
    border: 1px solid #EAECF0;
    border-radius: 8px;
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    width: 700px;
    height: 70px;
}

#links_section h5 {
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    align-items: center;
    text-align: center;
    color: #101828;
}

#contact {
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    align-items: center;
    text-align: center;
    color: #101828; 
    padding: 25px 25px;  
}

#links_section p {
    font-weight: 400;
    font-size: 14px;
    align-items: center;
    text-align: center;
    color: #101828;
}

#twitter h5 {
    padding: 25px 25px;
}

#books h5, #book__python h5, #pitch h5, #book__design h5, #btn__zuri h5 {
    padding-top: 15px;
    padding-bottom: 5px;
}

#books p, #book__python p, #pitch p, #book__design p, #btn__zuri p {
    padding-bottom: 15px;
    margin-top: -10px;
}

#line {
    background-color: #ffffff;
    border-bottom: 1px solid #EAECF0;
}

#footer {
    margin-top: 50px;
    margin-bottom: 50px;
}

#footer p {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #667085;
    position: relative;
    top: 10px;
}

@media (max-width: 575px) {

    #web-share {
        display: none;
    }
    
    #mobile-share {
        display: block;
        position: relative;
        top: -200px;
        left: 130px;
    }

    #links_section a {
        width: 305px;
    }

    .button {
        width: 305px;
    }

    #links_section h5 {
        font-size: 14px;
        line-height: 16px;
    }

    #contact {
        font-size: 14px;
        line-height: 16px;
    }
    
    #links_section p {
        font-size: 12px;
    }

    #books h5, #book__python h5, #book__design h5, #btn__zuri h5 {
        padding-top: 10px;
        padding-bottom: 5px;
       
    }

    #pitch h5 {
        padding-top: 15px;
        padding-bottom: 5px;   
    }
    
    #books p, #book__python p, #pitch p, #book__design p, #btn__zuri p {
        padding-bottom: 10px;
        padding-left: 6px;
        padding-right: 6px;
        margin-top: -12px;
    }

    #footer {
        display: flex;
        flex-direction: column;
        margin-left: 35px;
    }

    #footer p {
        font-size: 14px;
        line-height: 20px;
        text-align: start;
    }

    #zuri-logo {
        display: block;
        width: 138.77px;
        height: 23px;
    }

    #i4g-logo {
        display: block;
        width: 99.1px;
        height: 24px;
    }

}
#contact-sign-up {
    display: block;
    width: 760px;
    margin: 0 auto;
    padding-top: 80px;
}

#contact-sign-up h1 {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: -0.02em;
    color: #101828;
}

.contact-sign-p {
    font-family: Poppins;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #475467;
    padding-bottom: 15px;
}

.first-label, .last-label {
    display: block;
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #344054;
    padding-bottom: 7px;
}

#first_name {
    padding: 10px 14px;
    grid-gap: 8px;
    gap: 8px;
    width: 350px;
    height: 44px;
    background: #FFFFFF;
    border: 1px solid #D0D5DD;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    border-radius: 8px;
    margin-right: 30px;
}

#last_name {
    padding: 10px 14px;
    grid-gap: 8px;
    gap: 8px;
    width: 350px;
    height: 44px;
    background: #FFFFFF;
    border: 1px solid #D0D5DD;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    border-radius: 8px;
}

.email-label {
    display: block;
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #344054;
    padding-bottom: 7px;
    padding-top: 24px;
}

#email {
    padding: 10px 14px;
    grid-gap: 8px;
    gap: 8px;
    width: 730px;
    height: 44px;
    background: #FFFFFF;
    border: 1px solid #D0D5DD;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    border-radius: 8px;
}

.message-label {
    display: block;
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #344054;
    padding-bottom: 7px;
    padding-top: 24px;
}

#message {
    padding: 10px 14px;
    grid-gap: 8px;
    gap: 8px;
    width: 730px;
    height: 130px;
    background: #FFFFFF;
    border: 1px solid #D0D5DD;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    border-radius: 8px;
}

.check-con {
    padding-top: 15px;
}

.check-con p {
    position: relative;
    top: 7px;
    margin-left: 7px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #475467;
}

#btn__submit {
    padding: 12px 20px;
    grid-gap: 8px;
    gap: 8px;
    width: 730px;
    height: 48px;
    background: #1570EF;
    border: 1px solid #1570EF;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    border-radius: 8px;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 100px;
}

.button {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
}

input:focus, textarea:focus {
    outline: none;
    border: 1px solid #84CAFF;
    background: #FFFFFF;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05), 0px 0px 0px 4px #D1E9FF;
    border-radius: 8px;
}

button:hover {
    background: #175CD3;
    border: 1px solid #175CD3;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    border-radius: 8px
}

button:active {
    background: #1570EF;
    border: 1px solid #1570EF;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05), 0px 0px 0px 4px #D1E9FF;
    border-radius: 8px;
}

.firstname p, .lastname p, .email p, .message p {
    font-family: Poppins;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    margin-top: 10px;
    color: #f00e0e;     
}

.success {
    color: #4BB543;
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    position: relative;
    top: 50px;
}

@media (max-width: 575px) {

    #contact-sign-up {
        width: 305px;
        padding-top: 60px;
    }

    #first_name {
        width: 305px;
        margin-right: 0px;
    }

    .last-label {
       padding-top: 24px;
    }

    #last_name {
        display: block;
        width: 305px;
    }

    #email, #message, #btn__submit {
        width: 305px;
    }

    .contact-sign-p {
        font-size: 16px;
    }

    #btn__submit {
        margin-bottom: 70px;
    }

    .check-con p {
        position: relative;
        top: 0px;
        padding-top: 38px;
        margin-left: 7px;
    }

    #message {
        margin-bottom: -40px;
    }

    .success {
        font-size: 24px;
        text-align: center;
    }
}
